export default {
  name: 'HeaderBar',
  props: ['form', 'reload'],
  data() {
    return {
      isFocused: false
    }
  },
  computed: {
    isNotUrl: function() {
      if(this.form.url.length >= 1) {
        return !this.form.url.match(this.pattern);
      }
    }
  },
  mounted() {
    //this.focusInput();
  },
  methods: {
    setFocus(focus) {
      this.isFocused = focus;
    },
    submit() {
      this.$bus.$emit('submit-brew');
    },
    async logout() {
      if(this.isAuthenticated) {
        await this.$auth.logout().then(() => {
          this.$router.push({path: '/account/login'});
        });
      }
    }

  }
}
